import { Link } from 'react-router-dom';

import style from '../Header/Header.module.scss';
import logoDesktop from '../../assets/img/logo_desktop.svg';
import logoMobil from '../../assets/img/logo_mobile.svg';

const pages = [
  { id: 1, name: 'NavItem1', link: '/navitem1' },
  { id: 2, name: 'NavItem2', link: '/navitem2' },
  { id: 3, name: 'NavItem3', link: '/navitem3' },
];

export const Header = () => {
  return (
    <div className={style.container}>
      <div className={'container'}>
        <div className={style.inner}>
          <a href="/">
            <picture>
              <source media="(max-width: 1170px)" srcSet={logoMobil} />
              <img src={logoDesktop} alt="logo" />
            </picture>
          </a>

          <nav className={style.nav}>
            <ul className={style.list}>
              {pages.map((page) => {
                return (
                  <li key={page.id} className={style.item}>
                    <Link className={style.link} to={page.link}>
                      {page.name}
                    </Link>
                  </li>
                );
              })}
            </ul>
          </nav>
          <button className={style.button}>Button</button>
        </div>
      </div>
    </div>
  );
};
