import Slider from 'react-slick';

import style from '../Intro/Benefits-slider.module.scss';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import { Icons } from '../Icons';
import { benefitsData } from './banefitsData';

const settings = {
  dots: true,
  arrows: true,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 450,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
};

export const BenefitsSlider = () => {
  return (
    <ul className={style.list}>
      <Slider {...settings}>
        {benefitsData.map((item, index) => (
          <li className={style.item} key={index}>
            <div className={style.container}>
              <Icons
                name={item.icon}
                size="80"
                fill="none"
                stroke="#2E3A59"
                className={style.pic}
              />
              <p className={style.text}>{item.content}</p>
            </div>
          </li>
        ))}
      </Slider>
    </ul>
  );
};
