import style from './Comments.module.scss';
import { CommentData } from '../../type';
import avatar from '../../assets/img/avatar_comment.png';
import { Icons } from '../Icons';

const commentData: CommentData[] = [
  {
    name: 'Jane Doe',
    comment: `Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laboriosam quas, aut consectetur
    animi  autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae deleniti, iste nisi
    expedita, provident excepturi officia!
    Porro ab rerum omnis magnam eligendi error nobis dolore? Porro ab rerum omnis magnam eligendi error nobis dolore? Porro ab rerum omnis magnam eligendi error nobis dolore?
   `,
  },
  {
    name: 'Jane Doe',
    comment: `Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laboriosam quas, aut consectetur
    animi  autem aliquid`,
  },
  {
    name: 'Jane Doe',
    comment: `Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laboriosam quas, aut consectetur
    animi  autem aliquid`,
  },
];

export const Comment = () => {
  return (
    <ul className={style.list}>
      {commentData.map((item, index) => (
        <li className={style.item} key={index}>
          <img className={style.image} src={avatar} alt="comment" />
          <div className={style.wrapper}>
            <Icons name="quotes" className={style.icon} size="40"></Icons>
            <Icons name="quotes" className={style.icon} size="40"></Icons>
            <p className={style.text}>{item.comment}</p>
            <p className={style.name}>{item.name}</p>
          </div>
        </li>
      ))}
    </ul>
  );
};
