import style from '../Intro/Intro.module.scss';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import introPic from '../../assets/img/intro__desktop.png';
import introPicMobile from '../../assets/img/intro__mobile.png';

import { Benefits } from './Benefits';
import { BenefitsSlider } from './Benefits-slider';

export const Intro = () => {
  return (
    <div className="container--full">
      <div className={style.wrapper}>
        <h1 className={style.title}>Lorem ipsum, dolor sit amet consectetur</h1>
        {window.innerWidth < 960 && (
          <picture className={style.pic}>
            <source media="(max-width: 400px)" srcSet={introPicMobile} type="image/webp" />
            <img src={introPic} alt=" " />
          </picture>
        )}
        <div className={style.container}>
          {window.innerWidth > 960 && <img className={style.pic} src={introPic} alt=" " />}
          {window.innerWidth > 960 ? <Benefits /> : <BenefitsSlider />}
        </div>
      </div>
    </div>
  );
};
