import React from 'react';
import { AccordionData } from '../../type';
import { Icons } from '../Icons';

import style from './Accordions.module.scss';

const accordionData: AccordionData[] = [
  {
    title: 'Lorem ipsum, dolor sit amet adipisicing elit.',
    content: `Laboriosam quas, aut consectetur animi autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae delenit`,
    icon: 'faq-1',
  },
  {
    title: 'Lorem ipsum, dolor sit amet adipisicing elit.',
    content: `Laboriosam quas, aut consectetur animi autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae delenit`,
    icon: 'faq-2',
  },
  {
    title: 'Lorem ipsum, dolor sit amet adipisicing elit.',
    content: `Laboriosam quas, aut consectetur animi autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae delenit`,
    icon: 'faq-3',
  },
  {
    title: 'Lorem ipsum, dolor sit amet adipisicing elit.',
    content: `Laboriosam quas, aut consectetur animi autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae delenit`,
    icon: 'faq-4',
  },
  {
    title: 'Lorem ipsum, dolor sit amet adipisicing elit.',
    content: `Laboriosam quas, aut consectetur animi autem aliquid consequuntur suscipit exercitationem laborum cupiditate magnam eaque quae delenit`,
    icon: 'faq-5',
  },
];

const Accoordions = () => {
  const [active, setActive] = React.useState(100);

  const clickTitle = (event: any) => {
    setActive(+event.target.dataset.index);
    if (event.target.className.includes('active-title')) {
      setActive(100);
    }
  };
  return (
    <ul className={style.list}>
      {accordionData.map((items, index) => (
        <li className={style.item} key={index}>
          <div className={style.wrapper}>
            <Icons
              name={items.icon}
              size="40"
              fill="#A6ABBD"
              // stroke="#2E3A59"
              className={style.icon}
            />
            <div
              className={`${active === index ? 'active-title' : ' '} ${style.title}`}
              onClick={clickTitle}
              data-index={index}>
              {items.title}
            </div>
          </div>

          <div className={`${style.content} ${active === index ? 'active-content' : ' '}`}>
            {items.content}
          </div>
        </li>
      ))}
    </ul>
  );
};

export default Accoordions;
